import { Box, Typography, Avatar, List, ListItem, ListItemAvatar, ListItemText, CircularProgress } from '@mui/material';
import { useProxyWatch, useProxyWatchUpdates, useTranslation } from '@fanfanlo';
import { matrixUser } from '../../client/user';
import { matrixRoom } from '../../client/room';
import { RoomMembership } from '../../client/room/types';

interface RoomMember {
  userId: string;
  displayName?: string;
  avatarUrl?: string;
  membership: RoomMembership;
}

interface RoomMembersProps {
  roomId: string;
}

export function RoomMembers({ roomId }: RoomMembersProps) {
  const { t } = useTranslation('homepage/im/components/room/content');
  
  // 监听登录状态
  const [loginResponse] = useProxyWatch(
    matrixUser.userData,
    'userInfo.loginResponse',
    matrixUser.userData.userInfo.loginResponse
  );
  
  // 监听房间成员数据和加载状态
  const [roomMembers] = useProxyWatchUpdates(
    matrixRoom.roomData.roomMembers,
    [`${roomId}`],
    matrixRoom.roomData.roomMembers[roomId] || []
  );
  const [isFetchingMembers] = useProxyWatch(
    matrixRoom.roomData.isFetchingMembers,
    'value',
    matrixRoom.roomData.isFetchingMembers.value
  );

  // 如果登录信息未加载完成，显示加载中
  if (!loginResponse) {
    return (
      <Box sx={{ p: 2, textAlign: 'center' }}>
        <CircularProgress size={24} />
      </Box>
    );
  }

  const currentUserId = loginResponse.user_id;
  
  // 将当前用户排在最前面
  const sortedMembers = [...roomMembers].sort((a) => 
    a.userId === currentUserId ? -1 : 1
  );

  if (isFetchingMembers) {
    return (
      <Box sx={{ p: 2, textAlign: 'center' }}>
        <CircularProgress size={24} />
        <Typography variant="caption" color="text.secondary" sx={{ mt: 1 }}>
          {t('RoomMembers.loading')}
        </Typography>
      </Box>
    );
  }

  if (sortedMembers.length === 0) {
    return (
      <Box sx={{ p: 2, textAlign: 'center' }}>
        <Typography color="text.secondary">
          {t('RoomMembers.noMembers')}
        </Typography>
      </Box>
    );
  }

  return (
    <Box>
      <List>
        {sortedMembers.map((member: { userId: string; displayName?: string; avatarUrl?: string; membership: 'join' | 'invite' | 'leave' | 'ban' }) => (
          <ListItem key={member.userId}>
            <ListItemAvatar>
              <Avatar src={member.avatarUrl} alt={member.displayName || member.userId} />
            </ListItemAvatar>
            <ListItemText
              primary={member.displayName || member.userId}
              secondary={member.userId === currentUserId ? t('RoomMembers.you') : t(`RoomMembers.${member.membership}`)}
            />
          </ListItem>
        ))}
      </List>
    </Box>
  );
}