import { AppBar, Box, Container, CssBaseline, Fab, Fade, Slide, Toolbar, Typography, useScrollTrigger } from "@mui/material";
import { PropsWithChildren, ReactNode } from "react";
// import {AndroidPageContent} from "../../";
import { browserHistoryUtils, IDispatcher } from "@fanfanlo";
import KeyboardArrowLeft from '@mui/icons-material/KeyboardArrowLeft';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import * as React from "react";
import { useRouter } from "next/router";
import { PageShell } from "../app/PageShell";
import shadows from "@mui/material/styles/shadows";
import AndroidPageContent from "../page/AndroidPageContent";
import { IAppBarBackAskEvent } from "@app/static";
import { ITitleI18nConf } from "../page";
interface Props {
    /**
     * Injected by the documentation to work in an iframe.
     * You won't need it on your project.
     */
    window?: () => Window;
    children?: React.ReactElement<unknown>;
}

function HideOnScroll(props: Props) {
    const { children, window } = props;
    // Note that you normally won't need to set the window ref as useScrollTrigger
    // will default to window.
    // This is only being set here because the demo is in an iframe.
    const trigger = useScrollTrigger({
        target: window ? window() : undefined,
    });

    return (
        <Slide appear={false} direction="down" in={!trigger}>
            {children ?? <div />}
        </Slide>
    );
}
export interface IAppbarProps extends PropsWithChildren {
    title: ReactNode
    back: boolean
    backHandler?: () => boolean
    backDispatcher?:IDispatcher
}
export function Appbar({ title }: { title: ReactNode } & PropsWithChildren) {
    return (

        <AppBar color={"inherit"}>
            <Toolbar>
                <Typography variant="h6" component="div">
                    {title}
                </Typography>
            </Toolbar>
        </AppBar>
    )
}

interface IAppbarContainerProps extends PropsWithChildren {
    appbarProps: IAppbarProps
    titleConf?:ITitleI18nConf
    reload?: boolean
}

// export interface IAppBarBackAskEvent{
//     defaultPrevented:boolean
// }

function ScrollTop(props: Props) {
    const { children, window } = props;
    // Note that you normally won't need to set the window ref as useScrollTrigger
    // will default to window.
    // This is only being set here because the demo is in an iframe.
    const trigger = useScrollTrigger({
        target: window ? window() : undefined,
        disableHysteresis: true,
        threshold: 100,
    });

    const handleClick = (event: React.MouseEvent<HTMLDivElement>) => {
        const anchor = (
            (event.target as HTMLDivElement).ownerDocument || document
        ).querySelector('#back-to-top-anchor');

        if (anchor) {
            anchor.scrollIntoView({
                block: 'center',
            });
        }
    };

    return (
        <Fade in={trigger}>
            <Box data-id="content-scroll-top-button"
                onClick={handleClick}
                role="presentation"
                sx={{ position: 'fixed', bottom: 16, right: 16 }}
            >
                {children}
            </Box>
        </Fade>
    );
}
export function AppbarContainer(props: IAppbarContainerProps) {
    const { children, appbarProps, titleConf, reload } = props;
    const router = useRouter()
    function handleBack(){
        // 本想抽象成底层一个方法，但是考虑了一下router需要注入或者是一个callback，似乎像现在这样判断一下event也可以。回头继续观察一下吧。
        const event = browserHistoryUtils.askHistoryBack()
        if(event.defaultPrevented)return
        if(appbarProps.backDispatcher){
            const e:IAppBarBackAskEvent = {defaultPrevented:false}
            appbarProps.backDispatcher.dispatch('back',e)
            if(e.defaultPrevented)return
        }
        router.back()
    }
    return (
        <PageShell>
        <React.Fragment>
            <CssBaseline />
            <HideOnScroll {...props}>
                <AppBar color="inherit">
                    <Toolbar>
                        <KeyboardArrowLeft onClick={handleBack} />
                        {appbarProps.title}
                    </Toolbar>
                </AppBar>
            </HideOnScroll>
            <Toolbar id="back-to-top-anchor" />
            <Container sx={{padding:"0", marginTop:"17px"}}>
                <AndroidPageContent titleConf={titleConf} reload={reload}>
                    {children}
                </AndroidPageContent>
            </Container>

            <ScrollTop {...props}>
                <Fab size="small" aria-label="scroll back to top">
                    <KeyboardArrowUpIcon />
                </Fab>
            </ScrollTop>
        </React.Fragment>
        </PageShell>
    );
}
