import { Box, Paper, Divider, useTheme } from '@mui/material';
import { useTranslation } from '@fanfanlo';
import { RoomHeader } from './RoomHeader';
import { MessageList } from './MessageList';
import { MessageInput } from './MessageInput';
import { RoomMembers } from './RoomMembers';
import { useState } from 'react';
import { useRouter } from 'next/router';

interface RoomContentProps {
  roomId: string;
}

export function RoomContent({ roomId }: RoomContentProps) {
  const { t } = useTranslation('homepage/im/components/room/content');
  const theme = useTheme();
  const [showMembers, setShowMembers] = useState(false);

  const router = useRouter();
  const toggleMembers = () => {
    setShowMembers(!showMembers);
  };

  return (
    <Box sx={{ 
      display: 'flex', 
      flexDirection: 'column',
      height: '100vh',
      backgroundColor: theme.palette.background.default
    }}>
      {/* 顶部标题栏 */}
      <RoomHeader 
        roomId={roomId} 
        onToggleMembers={toggleMembers}
        showMembers={showMembers}
      />
      
      <Box sx={{ 
        display: 'flex', 
        flex: 1, 
        overflow: 'hidden',
        position: 'relative'
      }}>
        {/* 主消息区域 */}
        <Box sx={{
          flex: 1,
          display: 'flex',
          flexDirection: 'column',
          overflow: 'hidden',
          transition: 'margin 0.3s ease-in-out',
          marginRight: showMembers ? '300px' : 0,
        }}>
          <MessageList roomId={roomId} />
          <Divider />
          <MessageInput roomId={roomId} />
        </Box>
        
        {/* 右侧成员列表 */}
        <Box
          sx={{
            width: '300px',
            height: '100%',
            position: 'absolute',
            right: showMembers ? 0 : '-300px',
            top: 0,
            transition: 'right 0.3s ease-in-out',
            backgroundColor: theme.palette.background.paper,
            borderLeft: `1px solid ${theme.palette.divider}`,
            overflowY: 'auto',
            zIndex: 1,
          }}
        >
          <RoomMembers roomId={roomId} />
        </Box>
      </Box>
    </Box>
  );
}