import React from 'react';
import { IMatrixMessage } from '../../client/message/types';
import { TextMessage } from './text/TextMessage';
import { LocationMessage } from './location/LocationMessage';
import { messageUtils } from '../../client/message/messageUtils';

interface MessageRendererProps {
  message: IMatrixMessage;
  className?: string;
}

export const MessageRenderer = ({ 
  message, 
  className = '' 
}: MessageRendererProps) => {
  const commonProps = {
    message,
    className
  };

  if (messageUtils.isTextMessage(message)) {
    return <TextMessage key={message.event_id} {...commonProps} message={message} />;
  }

  if (messageUtils.isLocationMessage(message)) {
    return <LocationMessage key={message.event_id} {...commonProps} message={message} />;
  }
  
  // 其他消息类型的处理...
  
  return <div className={`unknown-message ${className}`}>
    不支持的消息类型: {message.content.msgtype}
  </div>;
};
