import { AppBar, Toolbar, IconButton, Typography, Avatar, Box } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import PeopleIcon from '@mui/icons-material/People';
import { useTranslation } from '@fanfanlo';
import { useProxyWatch } from '@fanfanlo';
import { matrixRoom } from '../../client/room';

export interface RoomHeaderProps {
  roomId: string;
  showMembers: boolean;
  onToggleMembers: () => void;
}

export function RoomHeader({ roomId, showMembers, onToggleMembers }: RoomHeaderProps) {
  const { t } = useTranslation('homepage/im/components/room/content');
  const [roomInfo] = useProxyWatch(
    matrixRoom.roomData,
    'joinedRooms',
    matrixRoom.roomData.joinedRooms.find(r => r.roomId === roomId)
  );

  return (
    <AppBar position="static" color="default" elevation={1}>
      <Toolbar>
        <IconButton
          size="large"
          edge="start"
          color="inherit"
          aria-label="menu"
          sx={{ mr: 2 }}
          onClick={onToggleMembers}
        >
          <MenuIcon />
        </IconButton>
        <Box sx={{ mr: 2 }}>
          <Avatar src={roomInfo?.avatarUrl}>
            {roomInfo?.name?.charAt(0) || 'R'}
          </Avatar>
        </Box>
        <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
          {roomInfo?.name || t('RoomContent.unnamedRoom')}
        </Typography>
        <IconButton
          color={showMembers ? 'primary' : 'default'}
          onClick={onToggleMembers}
          title={t('RoomContent.toggleMembers')}
        >
          <PeopleIcon />
        </IconButton>
      </Toolbar>
    </AppBar>
  );
}
