import { Dispatcher } from "@fanfanlo"
import { DispatcherUnsubscribe } from "@fanfanlo"

const dipsatcher = new Dispatcher()
interface IStaticModel{
    dipsatcher:Dispatcher
    screenLockCount:number
    switchPage:()=>void
    listenSwitchPage:(callback:()=>void)=>DispatcherUnsubscribe 
}
export const staticModel:IStaticModel = {
    dipsatcher,
    screenLockCount:0,
    switchPage:async()=>{
        dipsatcher.dispatch("switchPage")
        staticModel.screenLockCount = 0
    },
    listenSwitchPage:(callback:()=>void)=>{
        return dipsatcher.addListener("switchPage",callback)
    }
}   
