import { Log, useProxyWatch, useProxyWatchUpdates, useTranslation } from '@fanfanlo';
import { Box, CircularProgress, Typography } from '@mui/material';
import { useEffect, useRef, useState } from 'react';
import { matrixRoom } from '../../client/room';
import { matrixUser } from '../../client/user';
import { MessageRenderer } from '../messages/MessageRenderer';
import { messageTypeUtils } from '../../client/message/types';
const fileLogger = new Log(false, 'im.components.room.MessageList')
// fileLogger.childrenPaused = true;
// fileLogger.pause = true;
interface MessageListProps {
  roomId: string;
}

export function MessageList({ roomId }: MessageListProps) {
  const logger = fileLogger.sub(false, 'MessageList_fn');
  logger.pause = true;
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const messagesContainerRef = useRef<HTMLDivElement>(null);
  const [autoScroll, setAutoScroll] = useState(true);
  const { t } = useTranslation('homepage/im/components/room/content');
  
  // 监听登录状态
  const [loginResponse] = useProxyWatch(
    matrixUser.userData,
    'userInfo.loginResponse',
    matrixUser.userData.userInfo.loginResponse
  );
  
  // 监听消息列表
  const [messageIds] = useProxyWatchUpdates(
    matrixRoom.roomData.messages,
    [`${roomId}`],
    matrixRoom.roomData.messages[roomId] || []
  );
  logger.log('messageIds=', messageIds);
  logger.log(`matrixRoom.roomData.messages[roomId]=`,matrixRoom.roomData.messages[roomId])
  // 监听消息映射
  const [messagesMap] = useProxyWatchUpdates(
    matrixRoom.roomData,
    'messagesMap',
    matrixRoom.roomData.messagesMap
  );
  logger.log('messagesMap=', messagesMap);
  logger.log('matrixRoom.roomData.messagesMap=', matrixRoom.roomData.messagesMap)
  // 获取消息详情并转换为 IMatrixMessage 格式
  const messageList = messageIds?.map(id => {
    const message = messagesMap[id];
    if (!message) {
      console.warn('Message not found:', id);
      return null;
    }
    
    // 转换为 IMatrixMessage 格式
    return {
      id: message.event_id || message.id,
      type: message.content.msgtype,
      sender: message.sender,
      timestamp: message.origin_server_ts || message.timestamp,
      roomId: roomId,
      content: message.content,
      status: 'received' // 默认为已接收状态
    };
  }).filter(Boolean) || [];

  // 添加消息更新日志
  useEffect(() => {
    logger.log('messageIds=', messageIds);
    logger.log('messagesMap=', messagesMap);
  }, [messageIds, messagesMap]);
  
  // 滚动到底部
  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    if (autoScroll) {
      scrollToBottom();
    }
  }, [messageList, autoScroll]);

  // 处理滚动事件
  const handleScroll = () => {
    if (!messagesContainerRef.current) return;
    
    const { scrollTop, scrollHeight, clientHeight } = messagesContainerRef.current;
    const isAtBottom = scrollHeight - scrollTop === clientHeight;
    
    setAutoScroll(isAtBottom);
  };

  return (
    <Box 
      ref={messagesContainerRef}
      onScroll={handleScroll}
      sx={{
        flex: 1,
        overflowY: 'auto',
        p: 2,
        display: 'flex',
        flexDirection: 'column',
        gap: 2
      }}
    >
      {!loginResponse?.user_id ? (
        <Box display="flex" justifyContent="center" alignItems="center" height="100%">
          <CircularProgress />
        </Box>
      ) : messageList.length === 0 ? (
        <Box display="flex" justifyContent="center" alignItems="center" height="100%">
          <Typography variant="body1" color="text.secondary">
            {t('MessageList.noMessages')}
          </Typography>
        </Box>
      ) : (
        messageList.map((message) => (
          <Box
            key={message.id}
            sx={{
              display: 'flex',
              justifyContent: message.sender === loginResponse.user_id ? 'flex-end' : 'flex-start',
              mb: 2
            }}
          >
            <Box
              sx={{
                maxWidth: '80%',
                bgcolor: message.sender === loginResponse.user_id ? 'primary.main' : 'background.paper',
                color: message.sender === loginResponse.user_id ? 'primary.contrastText' : 'text.primary',
                p: 2,
                borderRadius: 2,
                boxShadow: 1,
                position: 'relative',
                wordBreak: 'break-word'
              }}
            >
              <MessageRenderer 
                message={message} 
                className={message.sender === loginResponse.user_id ? 'sent-message' : 'received-message'}
              />
              <Typography 
                variant="caption" 
                sx={{ 
                  display: 'block',
                  textAlign: 'right',
                  mt: 0.5,
                  opacity: 0.7,
                  color: message.sender === loginResponse.user_id ? 'primary.contrastText' : 'text.secondary'
                }}
              >
                {new Date(message.timestamp).toLocaleTimeString()}
              </Typography>
            </Box>
          </Box>
        ))
      )}
      <div ref={messagesEndRef} />
    </Box>
  );
}
