import { useRouter } from 'next/router';
import { useEffect } from 'react';
import { useTranslation } from "@fanfanlo";
import { AppbarContainer, AppShell } from "@app/ui";
import { RoomContent } from '../../../im/components/room';

export default function RoomPage() {
  const { t } = useTranslation("homepage/pages/im/room/content");
  const router = useRouter();
  const { id: roomId } = router.query;

  useEffect(() => {
    // 如果没有roomId，重定向到聊天列表
    if (!roomId && router.isReady) {
      router.push('/im');
    }
  }, [roomId, router]);

  if (!roomId) {
    return (
      <AppShell>
        <AppbarContainer appbarProps={{ title: t("content.title"), back: true }} />
      </AppShell>
    );
  }

  return (
    <AppShell>
      <RoomContent roomId={roomId as string} />
    </AppShell>
  );
}